/* https://create-react-app.dev/docs/adding-a-css-modules-stylesheet */

/* centrar */
.spinner {
  display: grid;
  justify-content: center;
  margin: 15px 10px 10px 10px;

}

.spinning {
  animation: spinner 4s linear infinite;
}

/* girar */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
